import React, { useState, useEffect } from 'react';
import axios from 'axios';

function DataTest() {
  const[data,setData] = useState('');

  useEffect(() => { //     

           axios.get('/api/test')
           .then(response => setData(response.data))
           .catch(error => console.log(error))

    },[])


return (
    <h2> data : {data}</h2>

);
}

export default DataTest;